import React, { useEffect, useState } from 'react';
import '../styles/task3.css';
import { Link } from 'react-router-dom';

const Task3 = () => {
    const [bgColor, setBgColor] = useState("green");
    const [value, setValue] = useState("");

    const changeColor = () => {
        if (value === "True" || value === "true" || value === "1") {
            setBgColor("red");
            console.log(value)
        } else if (value === "False" || value === "false" || value === "0") {
            setBgColor("blue");
        } else {
            setBgColor("green");
        }
    };

    useEffect(() => {
        changeColor();
    }, [value])


    return (
        <div className='container'>
            <h3><b>Q.<span style={{ color: "red" }}>Task 3</span></b></h3>
            <h4>If you will fill value any one (True, true and 1) then button background color will be change in <span style={{color:"red"}}>Red</span>.</h4>
            <h4>and If you will fill value any one (False, frue and 0) then button background color will be change in <span style={{color:"blue"}}>Blue</span>.</h4>
            <h4>and otherwise color will be <span style={{color:"green"}}>Green</span></h4>
            <Link to="/task2">
                <button id='task1'>Click For Task 2</button>
            </Link>
            <div id='check'>
                <input type="text"
                    value={value}
                    onChangeCapture={(e) => {
                        setValue(e.target.value);
                    }}
                />
                <br />
                <button style={{ backgroundColor: bgColor }} >Add value</button>
            </div>
        </div>
    )
}

export default Task3;