import React, { useState } from 'react';
import '../styles/task1.css';
import { Link } from 'react-router-dom';

const Task1 = () => {
    const [count, setCount] = useState(0);
    const [bgColor, setBgColor] = useState("orange");

    const incrementCount = () => {
        // Update state with incremented value
        setCount(count + 1);
    };

    const changeColor = () => {
        if (bgColor === "orange") {
            setBgColor("#00cca3")
        } else if (bgColor === "#00cca3") {
            setBgColor("red")
        } else {
            setBgColor("orange")
        }
    };


    return (

        <div className='container'>
            <h3><b>Q.<span style={{ color: "red" }}>Task 1</span></b></h3>
            <h4>Click the Link of under circle the Number will be increase.</h4>
            <h4>Click the Button the Circle background color will be change same as Button color.</h4>
            <div className='count'>
                <div id='card'>
                    <div id='number' style={{ backgroundColor: bgColor }}>
                        <h2>{count}</h2>
                        <a href='#' onClick={incrementCount}>Click to increase counter</a>
                    </div>
                </div>
                <div>
                    <button onClick={changeColor} style={{ backgroundColor: bgColor }}>Change color</button>
                </div>
            </div>
            <Link to="/task2">
                <button  id='task2'>Click For Task 2</button>
            </Link>
        </div>
    )
}

export default Task1;