import React, { useState } from 'react';
import ComponentB from './compB';
import '../styles/compA&B.css';
import { Link } from 'react-router-dom';

const ComponentA = () => {
    const [cendol, setCendol] = useState(2);

    const incrementCendol = () => {
        setCendol(cendol + 10);
    };

    return (
        <div>
            <h3><b>Q.<span style={{ color: "red" }}>Task 2</span></b></h3>
            <h4>Here are Two Components <span style={{ color: "orange" }}>A</span> and <span style={{ color: "green" }}>B</span>. <br />
                In <span style={{ color: "orange" }}>A component</span> have a variable called <span style={{ color: "red" }}><b>cendol = 2</b></span> and the value of
                variable has been passed to <span style={{ color: "green" }}>component B</span> to display the value. And In <span style={{ color: "green" }}>B component</span> there is a button
                on click of button the value of variable which has been got from <span style={{ color: "orange" }}>A component</span> should increment by
                10.</h4>
            <Link to="/">
                <button id='task1'>Click For Task 1</button>
            </Link>
            <ComponentB cendol={cendol} incrementCendol={incrementCendol} />
            <Link to="/task3">
                <button id='task3'>Click For Task3</button>
            </Link>
        </div>
    );
};

export default ComponentA;
